.plug {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 3rem;
  z-index: 90;
  background-color: #fff;
  transition: background-color 0.3s;
}

.plug__title {
  font-size: 2rem;
  font-weight: 400;
  font-style: normal;
  color: #000;
  text-align: center;
}

.dark .plug__title {
  color: #fff;
}

.plug.dark {
  background-color: #000;
}

@media screen and (max-width: 640px) and (orientation: landscape) {
  .plug {
    display: flex;
  }
}
